import * as React from "react";
import {
    Calendar,
    FocusTrapZone,
    Callout,
    DirectionalHint,
    defaultCalendarStrings,
} from "@fluentui/react";
import { useBoolean } from "@fluentui/react-hooks";
import { Button } from "@doar/components";

const CustomCalender: React.FunctionComponent = () => {
    const [selectedDate, setSelectedDate] = React.useState<Date>();
    const [
        showCalendar,
        { toggle: toggleShowCalendar, setFalse: hideCalendar },
    ] = useBoolean(false);
    const buttonContainerRef = React.useRef<HTMLDivElement>(null);

    const onSelectDate = React.useCallback(
        (date: Date, dateRangeArray: Date[]): void => {
            setSelectedDate(date);
            hideCalendar();
        },
        [hideCalendar]
    );

    return (
        <div>
            <div ref={buttonContainerRef}>
                <Button fullwidth onClick={toggleShowCalendar} color="white">
                    {new Date().toLocaleDateString()}
                </Button>
            </div>
            {showCalendar && (
                <Callout
                    isBeakVisible={false}
                    gapSpace={0}
                    doNotLayer={false}
                    target={buttonContainerRef}
                    directionalHint={DirectionalHint.bottomLeftEdge}
                    onDismiss={hideCalendar}
                    setInitialFocus
                >
                    <FocusTrapZone isClickableOutsideFocusTrap>
                        <Calendar
                            onDismiss={hideCalendar}
                            isMonthPickerVisible
                            value={selectedDate}
                            highlightCurrentMonth
                            isDayPickerVisible
                            showGoToToday
                            // Calendar uses English strings by default. For localized apps, you must override this prop.
                            strings={defaultCalendarStrings}
                        />
                    </FocusTrapZone>
                </Callout>
            )}
        </div>
    );
};

export default CustomCalender;
