import { StyledButton } from "src/layouts/welcome-area/style";
import { Search, Download, Plus } from "react-feather";
import { FC } from "react";

interface ITrainingWelcomeArea {
    onExportHnd: any;
    addNewDocumentHnd: any;
    disableAddNewBtn: boolean;
}

const TrainingWelcomeArea: FC<ITrainingWelcomeArea> = (
    props: ITrainingWelcomeArea
) => {
    const { onExportHnd, addNewDocumentHnd, disableAddNewBtn } = props;
    return (
        <>
            <StyledButton size="sm" color="white" path="/apps/mail" hasIcon>
                <Search />
                Search
            </StyledButton>
            <StyledButton
                size="sm"
                color="white"
                hasIcon
                ml="10px"
                onClick={onExportHnd}
            >
                <Download />
                Export
            </StyledButton>
            <StyledButton
                size="sm"
                ml="10px"
                hasIcon
                disabled={disableAddNewBtn}
                onClick={addNewDocumentHnd}
            >
                <Plus />
                Add New
            </StyledButton>
        </>
    );
};

export default TrainingWelcomeArea;
