import { Col, Row } from "@doar/components";
import { ColDef, ColGroupDef } from "ag-grid-community";
import { Archive, Eye, Trash } from "react-feather";

export interface ITrainingSummaryTable {
    category: string;
    subCategory: string;
    traineeType: string;
    trainingTitle: string;
    launchDate: Date;
    dueDate: Date;
    modifiedOn: Date;
    status: string;
    action: string;
}

export const trainingData: ITrainingSummaryTable[] = [
    {
        category: "Onboarding",
        subCategory: "Gender Sensitivity",
        traineeType: "Staff",
        trainingTitle: "Customer Service Staff Training",
        launchDate: new Date(),
        dueDate: new Date(),
        modifiedOn: new Date(),
        status: "70%",
        action: "Normal",
    },
    {
        category: "Special",
        subCategory: "Cultural Competency",
        traineeType: "Provider",
        trainingTitle: "Provider Training",
        launchDate: new Date(),
        dueDate: new Date(),
        modifiedOn: new Date(),
        status: "75%",
        action: "Normal",
    },
];

const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

const renderActions = () => {
    return (
        <Row>
            <Col lg={3} xl={3} mt="10px">
                <Eye color="blue" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Trash color="red" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Archive color="green" strokeWidth={1} size={22} />
            </Col>
        </Row>
    );
};
export const getColumnFields = (CheckBoxRow: any, RenderFavorite: any) => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            headerName: "Category",
            field: "category",
        },
        {
            headerName: "Sub-Category",
            field: "subCategory",
        },
        {
            headerName: "Trainee Type",
            field: "traineeType",
        },
        {
            headerName: "Training Title",
            field: "trainingTitle",
        },
        {
            headerName: "Launch Date",
            field: "launchDate",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Due Date",
            field: "dueDate",
            cellRenderer: toLocalDate,
        },

        {
            headerName: "Modified On",
            field: "modifiedOn",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Status",
            field: "status",
        },
        {
            headerName: "Action",
            field: "action",
            cellRenderer: renderActions,
        },
    ];
    return columnFields;
};
