import { Col, Row } from "@doar/components";
import { FC, useState } from "react";
import WelcomeArea from "src/layouts/welcome-area";
import AddNewTraining from "./addNewTraining";
import TrainingSummaryTable from "./trainingSummaryTable";
import TrainingWelcomeArea from "./welcomeArea";

const TrainingMain: FC = () => {
    const [openAddNewDocument, setAddNewDocument] = useState(false);
    const onCSVExport = () => {};
    return (
        <>
            <WelcomeArea
                welcomeText="Welcome To Training"
                title="Training"
                prevTitle="HEQ 360"
            >
                <TrainingWelcomeArea
                    onExportHnd={onCSVExport}
                    disableAddNewBtn={openAddNewDocument}
                    addNewDocumentHnd={() => {
                        setAddNewDocument(true);
                    }}
                />
            </WelcomeArea>
            <Row gutters={10}>
                <Col lg={12} xl={12} mt="10px">
                    {openAddNewDocument ? (
                        <AddNewTraining
                            onCancel={() => {
                                setAddNewDocument(false);
                            }}
                        />
                    ) : (
                        <TrainingSummaryTable />
                    )}
                </Col>
            </Row>
        </>
    );
};

export default TrainingMain;
