import { FC } from "react";
import WelcomeArea from "src/layouts/welcome-area";
import { Col, Row } from "@doar/components";
import { Search } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import ResourceCollapse from "./resourceCollapse";

const ResourcesMain: FC = () => {
    return (
        <>
            <WelcomeArea
                welcomeText="Welcome To Resources"
                title="Resources"
                prevTitle="HEQ 360"
            >
                <StyledButton size="lg" color="white" hasIcon>
                    <Search />
                    Search
                </StyledButton>
            </WelcomeArea>

            <Row gutters={12}>
                <Col lg={12} xl={12} mt="10px">
                    <ResourceCollapse />
                </Col>
            </Row>
        </>
    );
};

export default ResourcesMain;
