/* eslint-disable @typescript-eslint/restrict-plus-operands */
import { Col, FormGroup, Input, Label, Row } from "@doar/components";
import { FC, useState } from "react";
import { Dropdown, IDropdownStyles } from "@fluentui/react";
import { Upload } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import CustomModal from "./customModal";
import CustomCalender from "../../components/fluentUi-Calender/customCalender";

interface IAddNewTraining {
    onCancel: any;
}
const AddNewTraining: FC<IAddNewTraining> = (props: IAddNewTraining) => {
    const { onCancel } = props;
    const [alertModal, setOpenAlertModal] = useState(false);
    const onChangeCategory = (event: any, item: any): void => {};
    const onChangeSubCategory = (event: any, item: any): void => {};
    const onAlertCallack = (event: any, item: any): void => {
        if (item.text === "Yes") {
            setOpenAlertModal(true);
        }
    };
    const formData = [
        {
            label: "Category",
            type: "dropdown",
            id: "category",
            selectedLabel: "Select Category",
            onChangeCallback: onChangeCategory,
            menu: [
                {
                    key: "category1",
                    text: "Onboarding",
                },
                {
                    key: "category2",
                    text: "Annual Refresher",
                },
                {
                    key: "category3",
                    text: "Special",
                },
            ],
        },
        {
            label: "Sub Category",
            type: "dropdown",
            id: "subCategory",
            selectedLabel: "",
            menu: [
                { text: "Cultural Competency", key: "Policies1" },
                { text: "Gender Sensitivity", key: "Policies2" },
                { text: "Health Equity Principles", key: "Policies3" },
            ],
            onChangeCallback: onChangeSubCategory,
        },
        {
            label: "Trainee Type",
            type: "dropdown",
            id: "traineeType",
            menu: [
                { text: "Staff", key: "traineeType1" },
                { text: "Provider", key: "traineeType2" },
                { text: "Vendor", key: "traineeType3" },
                { text: "Other", key: "traineeType4" },
            ],
        },
        {
            label: "Training Title",
            type: "text",
            id: "trainingTitle",
        },
        {
            label: "Launch Date",
            type: "date",
            id: "launchDate",
        },
        {
            label: "Due Date",
            type: "date",
            id: "dueDate",
        },
        {
            label: "Attestation",
            type: "dropdown",
            id: "attestation",
            menu: [
                { text: "Yes", key: "attestation1" },
                { text: "No", key: "attestation2" },
            ],
        },
        {
            label: "Knowledge Test",
            type: "dropdown",
            id: "knowledgeTest",
            menu: [
                { text: "Yes", key: "knowledgeTest1" },
                { text: "No", key: "knowledgeTest2" },
            ],
        },
        {
            label: "Retest Interval",
            type: "text",
            id: "retestInterval",
            placeholder: "xx hours/days",
        },
        {
            label: "Training Material",
            type: "button",
            buttonText: "Select Training",
            id: "trainingMaterial",
            onClickHnd: () => {
                setOpenAlertModal(true);
            },
        },
        {
            label: "Trainee List",
            type: "button",
            buttonText: "Import Trainee",
            id: "traineeList",
        },
    ];

    // const [addNewTraining, setFormData] = useState(formData);

    const inputElement = (item: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={item.id}>
                    {item.label}
                </Label>
                <Input
                    type={item.type}
                    id={item.id}
                    placeholder={item.placeholder}
                    name={item.id}
                />
            </FormGroup>
        );
    };

    const dropdownStyles: Partial<IDropdownStyles> = {
        dropdown: { width: "100%" },
        root: { height: "100%" },
    };

    const dropdownElement = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Dropdown
                    // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                    placeholder={`Select an ${data.label}`}
                    options={data.menu}
                    styles={dropdownStyles}
                    onChange={data.onChangeCallback}
                />
            </FormGroup>
        );
    };

    const renderCalender = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <CustomCalender />
            </FormGroup>
        );
    };

    const renderButton = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>

                <StyledButton
                    fullwidth
                    hasIcon
                    color="white"
                    onClick={data.onClickHnd}
                >
                    <Upload />
                    {data.buttonText}
                </StyledButton>
            </FormGroup>
        );
    };
    const oneRowItem = 4;

    const renderElement = (item: any) => {
        if (item.type === "text") {
            return inputElement(item);
        }
        if (item.type === "dropdown") {
            return dropdownElement(item);
        }
        if (item.type === "date") {
            return renderCalender(item);
        }
        if (item.type === "button") {
            return renderButton(item);
        }
        return <></>;
    };

    const chunk: any = (array: any, chunksize: number) => {
        const newArray = [];
        while (array.length) {
            const chunks = array.splice(0, chunksize);
            newArray.push(chunks);
        }
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return newArray;
    };

    const splitedAaddNewTraining = chunk(formData, oneRowItem);
    return (
        <>
            <form action="#" noValidate>
                {splitedAaddNewTraining.map((oneRowData: [], index: number) => {
                    return (
                        // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                        <Row key={`abc${0 + index}`}>
                            {oneRowData.map((eleData) => {
                                return (
                                    <Col
                                        lg={3}
                                        xl={3}
                                        mt="10px"
                                        key={`col${0 + index}`}
                                    >
                                        {renderElement(eleData)}
                                    </Col>
                                );
                            })}
                        </Row>
                    );
                })}
                <Row>
                    <div style={{ width: "100%" }}>
                        <div style={{ float: "right", marginRight: 50 }}>
                            <StyledButton type="submit" margin={10} disabled>
                                Add Entry
                            </StyledButton>
                            <StyledButton
                                color="secondary"
                                margin={10}
                                onClick={onCancel}
                            >
                                Cancel
                            </StyledButton>
                        </div>
                    </div>
                </Row>
            </form>
            <CustomModal
                show={alertModal}
                modelCloseHnd={() => {
                    setOpenAlertModal(false);
                }}
            />
        </>
    );
};

export default AddNewTraining;
