import { Row, Col } from "@doar/components";
import { FC, useState } from "react";
import WelcomeArea from "src/layouts/welcome-area";
import AddNewAssessment from "./addNewAssessment";
import AssessmentSummaryTable from "./assessmentSummaryTable";
import AssessmentWelcomeArea from "./welcomeArea";

const AssessmentMain: FC = () => {
    const [openAddNewDocument, setAddNewDocument] = useState(false);
    const onCSVExport = () => {};
    return (
        <>
            <WelcomeArea
                welcomeText="Welcome To Assessment"
                title="Assessment"
                prevTitle="HEQ 360"
            >
                <AssessmentWelcomeArea
                    onExportHnd={onCSVExport}
                    disableAddNewBtn={openAddNewDocument}
                    addNewDocumentHnd={() => {
                        setAddNewDocument(true);
                    }}
                />
            </WelcomeArea>

            <Row gutters={10}>
                <Col lg={12} xl={12} mt="10px">
                    {openAddNewDocument ? (
                        <AddNewAssessment
                            onCancel={() => {
                                setAddNewDocument(false);
                            }}
                        />
                    ) : (
                        <AssessmentSummaryTable />
                    )}
                </Col>
            </Row>
        </>
    );
};

export default AssessmentMain;
