import { FC, ReactNode, useState } from "react";
import Breadcrumb from "../../components-doar/breadcrumb";
import ModalPrint from "../../components-doar/dashboard-one/modal-print";
import ModalReport from "../../components-doar/dashboard-one/modal-report";
import {
    StyledWelcomeArea,
    StyledWelcomeLeft,
    StyledWelcomeRight,
} from "./style";

export interface IWelcomeAreaProps {
    welcomeText: string;
    title: string;
    prevTitle: string;
    children?: ReactNode;
}

const WelcomeArea: FC<IWelcomeAreaProps> = (props: IWelcomeAreaProps) => {
    const [showPrintModal, setShowPrintModal] = useState(false);
    const [showReportModal, setShowReportModal] = useState(false);
    const handlePrintModal = () => {
        setShowPrintModal((prev) => !prev);
    };
    const handleReportModal = () => {
        setShowReportModal((prev) => !prev);
    };

    const { welcomeText, title, prevTitle, children } = props;
    return (
        <>
            <StyledWelcomeArea>
                <StyledWelcomeLeft>
                    <Breadcrumb
                        prev={[{ text: prevTitle, link: "/" }]}
                        title={title}
                        wcText={welcomeText}
                    />
                </StyledWelcomeLeft>
                <StyledWelcomeRight>{children}</StyledWelcomeRight>
            </StyledWelcomeArea>
            <ModalPrint show={showPrintModal} onClose={handlePrintModal} />
            <ModalReport show={showReportModal} onClose={handleReportModal} />
        </>
    );
};

export default WelcomeArea;
