import { Col, Row } from "@doar/components";
import { ColDef, ColGroupDef } from "ag-grid-community";
import { Archive, Download, Eye, Trash } from "react-feather";

export interface IAssessmentSummaryTable {
    category: string;
    assessment: string;
    completionDate: Date;
    reassessmentDate: Date;
    assignee: string;
    status: string;
    action: string;
}

export const assessmentData: IAssessmentSummaryTable[] = [
    {
        category: "Organizational Assessment",
        assessment: "WHO HEAT Tool",
        completionDate: new Date(),
        reassessmentDate: new Date(),
        assignee: "John smith",
        status: "Action",
        action: "Normal",
    },
];

const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

const renderActions = () => {
    return (
        <Row>
            <Col lg={3} xl={3} mt="10px">
                <Eye color="blue" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Download color="red" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Archive color="green" strokeWidth={1} size={22} />
            </Col>
        </Row>
    );
};
export const getColumnFields = (CheckBoxRow: any, RenderFavorite: any) => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            headerName: "Category",
            field: "category",
        },
        {
            headerName: "Assessment",
            field: "assessment",
        },
        {
            headerName: "Completion Date",
            field: "completionDate",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Re-Assessment Date",
            field: "reassessmentDate",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Assignee",
            field: "assignee",
        },
        {
            headerName: "Status",
            field: "status",
        },
        {
            headerName: "Action",
            field: "action",
            cellRenderer: renderActions,
        },
    ];
    return columnFields;
};
