import React, { FC, useState } from "react";
import {
    Modal,
    ModalHeader,
    ModalTitle,
    ModalClose,
    ModalBody,
    ModalFooter,
    Button,
} from "@doar/components";

interface ICustomModal {
    show: boolean;
    modelCloseHnd: any;
}

const CustomModal: FC<ICustomModal> = (props: ICustomModal) => {
    const { show, modelCloseHnd } = props;

    return (
        <>
            <Modal show={show} onClose={modelCloseHnd}>
                <ModalHeader>
                    <ModalTitle>Modal Title</ModalTitle>
                    <ModalClose>x</ModalClose>
                </ModalHeader>
                <ModalBody>
                    <p>
                        It is a long established fact that a reader will be
                        distracted by the readable content of a page when
                        looking at its layout. The point of using Lorem Ipsum is
                        that it has a more-or-less normal distribution of
                        letters, as opposed to using &apos;Content here, content
                        here&apos;, making it look like readable English.
                    </p>
                </ModalBody>
                <ModalFooter>
                    <Button color="secondary" onClick={modelCloseHnd}>
                        Close
                    </Button>
                    <Button color="primary" disabled>
                        Save changes
                    </Button>
                </ModalFooter>
            </Modal>
        </>
    );
};

export default CustomModal;
