import { ColDef, ColGroupDef } from "ag-grid-community";
import { Col, Row } from "@doar/components";
import { Eye, Trash } from "react-feather";

export interface IInitiativeSummaryTable {
    program: string;
    assignee: string;
    effectiveDate: Date;
    renewal: Date;
    status: "Active" | "Out of Date" | "Archived";
    action: string;
}

export const documentsData: IInitiativeSummaryTable[] = [
    {
        program: "2022 DEQ Program",
        assignee: "Mary Jones",
        effectiveDate: new Date(),
        renewal: new Date(),
        status: "Active",
        action: "",
    },
];

const toLocalDate = (params: any) => {
    const { value } = params;
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return value && value.toLocaleDateString();
};

const renderActions = () => {
    return (
        <Row>
            <Col lg={3} xl={3} mt="10px">
                <Eye color="blue" strokeWidth={1} size={22} />
            </Col>
            <Col lg={3} xl={3} mt="10px">
                <Trash color="red" strokeWidth={1} size={22} />
            </Col>
        </Row>
    );
};

export const getColumnFields = () => {
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            headerName: "Program",
            field: "program",
        },
        {
            headerName: "Assignee",
            field: "assignee",
        },
        {
            headerName: "Effective Date",
            field: "effectiveDate",
            // eslint-disable-next-line @typescript-eslint/no-unsafe-return
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Renewal Date",
            field: "renewal",
            cellRenderer: toLocalDate,
        },
        {
            headerName: "Status",
            field: "status",
        },
        {
            headerName: "Action",
            field: "action",
            cellRenderer: renderActions,
        },
    ];
    return columnFields;
};
