/* eslint-disable @typescript-eslint/restrict-plus-operands */
import { Col, FormGroup, Input, Label, Row, Textarea } from "@doar/components";
import { FC, useState } from "react";
import { Dropdown, IDropdownStyles } from "@fluentui/react";
import { Upload } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import CustomCalender from "../../components/fluentUi-Calender/customCalender";

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface IMetricsPivot {}
const MetricsPivot: FC<IMetricsPivot> = (props: IMetricsPivot) => {
    const onChangeCategory = (event: any, item: any): void => {};
    const onChangeSubCategory = (event: any, item: any): void => {};
    const oneRowItem = 5;

    const formData = [
        {
            label: "Metric",
            type: "text",
            id: "metric",
        },

        {
            label: "Goal",
            type: "text",
            id: "goal",
        },
        {
            label: "Baseline",
            type: "text",
            id: "baseline",
        },
        {
            label: "Remeasurement",
            type: "dropdown",
            id: "remeasurement",
            menu: [
                { text: "Monthly", key: "remeasurement1" },
                { text: "Quarterly", key: "remeasurement2" },
                { text: "Semi-Annually", key: "remeasurement3" },
                { text: "Annually", key: "remeasurement4" },
            ],
        },
        {
            label: "Owner",
            type: "dropdown",
            id: "owner",
            menu: [
                { text: "John", key: "owner1" },
                { text: "Mike", key: "owner2" },
                { text: "Sam", key: "owner3" },
                { text: "Stanley", key: "owner4" },
            ],
        },
        {
            label: "Brief Description",
            type: "textarea",
            maxCount: 150,
            id: "briefDescription",
            placeholder: "Add description",
        },
        {
            label: "Calculation",
            type: "textarea",
            id: "calculation",
            maxCount: 150,
            placeholder: "Add Calculation",
        },
    ];

    // const [MetricsPivotData, setFormData] = useState(formData);

    const inputElement = (item: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={item.id}>
                    {item.label}
                </Label>
                <Input
                    type={item.type}
                    id={item.id}
                    placeholder={item.placeholder}
                    name={item.id}
                />
            </FormGroup>
        );
    };

    const dropdownStyles: Partial<IDropdownStyles> = {
        dropdown: { width: "100%" },
        root: { height: "100%" },
    };

    const dropdownElement = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Dropdown
                    // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                    placeholder={`Select an ${data.label}`}
                    options={data.menu}
                    styles={dropdownStyles}
                    onChange={data.onChangeCallback}
                />
            </FormGroup>
        );
    };

    const renderCalender = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <CustomCalender />
            </FormGroup>
        );
    };

    const renderButton = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>

                <StyledButton fullwidth hasIcon>
                    <Upload />
                    {data.buttonText}
                </StyledButton>
            </FormGroup>
        );
    };

    const renderTextarea = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Textarea
                    name={data.label}
                    id={data.id}
                    rows={5}
                    placeholder={data.placeholder}
                />
            </FormGroup>
        );
    };

    const renderElement = (item: any) => {
        if (item.type === "text") {
            return inputElement(item);
        }
        if (item.type === "dropdown") {
            return dropdownElement(item);
        }
        if (item.type === "date") {
            return renderCalender(item);
        }
        if (item.type === "button") {
            return renderButton(item);
        }
        if (item.type === "textarea") {
            return renderTextarea(item);
        }
        return <></>;
    };

    const chunk: any = (array: any, chunksize: number) => {
        const newArray = [];
        while (array.length) {
            const chunks = array.splice(0, chunksize);
            newArray.push(chunks);
        }
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return newArray;
    };

    const splitedAMetricsPivotData = chunk(formData, oneRowItem);
    return (
        <>
            <form action="#" noValidate>
                {splitedAMetricsPivotData.map(
                    (oneRowData: [], index: number) => {
                        return (
                            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                            <Row key={`abc${0 + index}`} gutters={20}>
                                {oneRowData.map((eleData) => {
                                    return (
                                        <Col
                                            col={6}
                                            sm={4}
                                            md={3}
                                            lg
                                            key={`col${0 + index}`}
                                        >
                                            {renderElement(eleData)}
                                        </Col>
                                    );
                                })}
                            </Row>
                        );
                    }
                )}
            </form>
        </>
    );
};

export default MetricsPivot;
