import { Col, FormGroup, Input, Label, Row } from "@doar/components";
import { FC, useState } from "react";
import { Dropdown, IDropdownStyles } from "@fluentui/react";
import { Upload } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import CustomModal from "./customModal";
import CustomCalender from "../../components/fluentUi-Calender/customCalender";

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface IAddNewWorkPlan {}
const AddNewWorkPlan: FC<IAddNewWorkPlan> = (props: IAddNewWorkPlan) => {
    const [alertModal, setOpenAlertModal] = useState(false);
    const onChangeCategory = (event: any, item: any): void => {};
    const onChangeSubCategory = (event: any, item: any): void => {};
    const onAlertCallack = (event: any, item: any): void => {
        if (item.text === "Yes") {
            setOpenAlertModal(true);
        }
    };

    const formData = [
        {
            label: "Name",
            type: "text",
            id: "name",
        },
        {
            label: "Task Type",
            type: "dropdown",
            id: "taskType",
            menu: [
                { text: "Meeting", key: "taskType1" },
                { text: "Reporting", key: "taskType" },
                { text: "Activity", key: "taskType3" },
            ],
        },
        {
            label: "Due Date",
            type: "date",
            id: "dueDate",
        },
        {
            label: "Assignee",
            type: "dropdown",
            id: "assignee",
            menu: [
                { text: "Mary Jones", key: "assignee1" },
                { text: "John Snow", key: "assignee2" },
                { text: "Beth Page", key: "assignee3" },
            ],
        },
        {
            label: "Status",
            type: "dropdown",
            id: "status",
            menu: [
                { text: "Planned", key: "status1" },
                { text: "In Progress", key: "status2" },
                { text: "Complete", key: "status3" },
            ],
        },
        {
            label: "Alert",
            type: "dropdown",
            id: "alert",
            menu: [
                { text: "Yes", key: "alert1" },
                { text: "No", key: "alert2" },
            ],
            onChangeCallback: onAlertCallack,
        },
        {
            label: "Files",
            buttonText: "Upload",
            type: "button",
            id: "files",
        },
        {
            label: "Importance",
            type: "dropdown",
            id: "importance",
            menu: [
                { text: "High", key: "importance1" },
                { text: "Medium", key: "importance2" },
                { text: "Low", key: "importance3" },
            ],
        },
    ];

    // const [AddNewWorkPlanData, setFormData] = useState(formData);

    const inputElement = (item: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={item.id}>
                    {item.label}
                </Label>
                <Input
                    type={item.type}
                    id={item.id}
                    placeholder={item.placeholder}
                    name={item.id}
                />
            </FormGroup>
        );
    };

    const dropdownStyles: Partial<IDropdownStyles> = {
        dropdown: { width: "100%" },
        root: { height: "100%" },
    };

    const dropdownElement = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Dropdown
                    // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                    placeholder={`Select an ${data.label}`}
                    options={data.menu}
                    styles={dropdownStyles}
                    onChange={data.onChangeCallback}
                />
            </FormGroup>
        );
    };

    const renderCalender = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <CustomCalender />
            </FormGroup>
        );
    };

    const renderButton = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>

                <StyledButton fullwidth hasIcon color="white">
                    <Upload />
                    {data.buttonText}
                </StyledButton>
            </FormGroup>
        );
    };
    const oneRowItem = 5;

    const renderElement = (item: any) => {
        if (item.type === "text") {
            return inputElement(item);
        }
        if (item.type === "dropdown") {
            return dropdownElement(item);
        }
        if (item.type === "date") {
            return renderCalender(item);
        }
        if (item.type === "button") {
            return renderButton(item);
        }
        return <></>;
    };

    const chunk: any = (array: any, chunksize: number) => {
        const newArray = [];
        while (array.length) {
            const chunks = array.splice(0, chunksize);
            newArray.push(chunks);
        }
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return newArray;
    };

    const splitedAAddNewWorkPlanData = chunk(formData, oneRowItem);
    return (
        <>
            <form action="#" noValidate>
                {splitedAAddNewWorkPlanData.map(
                    (oneRowData: [], index: number) => {
                        return (
                            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                            <Row key={`abc${0 + index}`}>
                                {oneRowData.map((eleData) => {
                                    return (
                                        <Col
                                            col={6}
                                            sm={6}
                                            md={6}
                                            lg
                                            key={`col${0 + index}`}
                                        >
                                            {renderElement(eleData)}
                                        </Col>
                                    );
                                })}
                            </Row>
                        );
                    }
                )}
            </form>
            <CustomModal
                show={alertModal}
                modelCloseHnd={() => {
                    setOpenAlertModal(false);
                }}
            />
        </>
    );
};

const WorkPlanPivot = () => {
    return <AddNewWorkPlan />;
};

export default WorkPlanPivot;
