/* eslint-disable @typescript-eslint/restrict-plus-operands */
import { Col, FormGroup, Input, Label, Row } from "@doar/components";
import { FC, useState } from "react";
import { Dropdown, IDropdownStyles } from "@fluentui/react";
import { Upload } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import CustomModal from "./customModal";
import CustomCalender from "../../components/fluentUi-Calender/customCalender";

interface IAddNewDocument {
    onCancel: any;
}
const AddNewDocument: FC<IAddNewDocument> = (props: IAddNewDocument) => {
    const { onCancel } = props;
    const [alertModal, setOpenAlertModal] = useState(false);
    const onChangeCategory = (event: any, item: any): void => {};
    const onChangeSubCategory = (event: any, item: any): void => {};
    const onAlertCallack = (event: any, item: any): void => {
        if (item.text === "Yes") {
            setOpenAlertModal(true);
        }
    };

    const formData = [
        {
            label: "Category",
            type: "dropdown",
            id: "category",
            placeholder: "",
            selectedLabel: "Select Any",
            subMenuLinkedId: "subCategory",
            onChangeCallback: onChangeCategory,
            menu: [
                {
                    key: "category1",
                    text: "Policies",
                    subCategory: [
                        { text: "Staff training", key: "Policies1" },
                        { text: "Program Management", key: "Policies2" },
                    ],
                },
                {
                    key: "category2",
                    text: "Meeting Materials",
                    subCategory: [
                        {
                            text: "Quality Management Committee",
                            key: "meetingMaterial1",
                        },
                        {
                            text: "Board Quality Committee",
                            key: "meetingMaterial2",
                        },
                        {
                            text: "Health Equity Initiatives Monitoring",
                            key: "meetingMaterial3",
                        },
                    ],
                },
            ],
        },
        {
            label: "Sub Category",
            type: "dropdown",
            id: "subCategory",
            placeholder: "",
            selectedLabel: "",
            subMenuLinkedId: "",
            menu: [
                { text: "Staff training", key: "Policies1" },
                { text: "Program Management", key: "Policies2" },
            ],
            onChangeCallback: onChangeSubCategory,
        },
        {
            label: "Document Name",
            type: "text",
            id: "documentName",
            placeholder: "",
        },
        {
            label: "Effective Date",
            type: "date",
            id: "effectiveDate",
            placeholder: "",
        },
        {
            label: "Renewal Date",
            type: "date",
            id: "renewalDate",
            placeholder: "",
        },
        {
            label: "Assignee",
            type: "dropdown",
            id: "assignee",
            menu: [
                { text: "Mary Jones", key: "assignee1" },
                { text: "Beth Page", key: "assignee2" },
            ],
        },
        {
            label: "Alert",
            type: "dropdown",
            id: "alert",
            menu: [
                { text: "Yes", key: "alert1" },
                { text: "No", key: "alert2" },
            ],
            onChangeCallback: onAlertCallack,
        },
        {
            label: "Files",
            buttonText: "Upload",
            type: "button",
            id: "files",
        },
        {
            label: "Modified On",
            type: "date",
            id: "modifiedOn",
            placeholder: "",
        },
        {
            label: "Status",
            type: "dropdown",
            id: "status",
            menu: [
                { text: "Active", key: "status1" },
                { text: "Out Of Date", key: "status2" },
                { text: "Archived", key: "status3" },
            ],
        },
        {
            label: "Type",
            type: "dropdown",
            id: "type",
            menu: [
                { text: "Restricted", key: "type1" },
                { text: "Sensitive", key: "type2" },
                { text: "Normal", key: "type3" },
            ],
        },
    ];

    // const [addNewDocumentData, setFormData] = useState(formData);

    const inputElement = (item: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={item.id}>
                    {item.label}
                </Label>
                <Input
                    type={item.type}
                    id={item.id}
                    placeholder={item.placeholder}
                    name={item.id}
                />
            </FormGroup>
        );
    };

    const dropdownStyles: Partial<IDropdownStyles> = {
        dropdown: { width: "100%" },
        root: { height: "100%" },
    };

    const dropdownElement = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Dropdown
                    // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                    placeholder={`Select an ${data.label}`}
                    options={data.menu}
                    styles={dropdownStyles}
                    onChange={data.onChangeCallback}
                />
            </FormGroup>
        );
    };

    const renderCalender = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <CustomCalender />
            </FormGroup>
        );
    };

    const renderButton = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>

                <StyledButton fullwidth hasIcon color="white">
                    <Upload />
                    {data.buttonText}
                </StyledButton>
            </FormGroup>
        );
    };
    const oneRowItem = 4;

    const renderElement = (item: any) => {
        if (item.type === "text") {
            return inputElement(item);
        }
        if (item.type === "dropdown") {
            return dropdownElement(item);
        }
        if (item.type === "date") {
            return renderCalender(item);
        }
        if (item.type === "button") {
            return renderButton(item);
        }
        return <></>;
    };

    const chunk: any = (array: any, chunksize: number) => {
        const newArray = [];
        while (array.length) {
            const chunks = array.splice(0, chunksize);
            newArray.push(chunks);
        }
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return newArray;
    };

    const splitedAaddNewDocumentData = chunk(formData, oneRowItem);
    return (
        <>
            <form action="#" noValidate>
                {splitedAaddNewDocumentData.map(
                    (oneRowData: [], index: number) => {
                        return (
                            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                            <Row key={`abc${0 + index}`}>
                                {oneRowData.map((eleData) => {
                                    return (
                                        <Col
                                            lg={3}
                                            xl={3}
                                            mt="10px"
                                            key={`col${0 + index}`}
                                        >
                                            {renderElement(eleData)}
                                        </Col>
                                    );
                                })}
                            </Row>
                        );
                    }
                )}
                <Row>
                    <div style={{ width: "100%" }}>
                        <div style={{ float: "right", marginRight: 50 }}>
                            <StyledButton type="submit" margin={10} disabled>
                                Add Entry
                            </StyledButton>
                            <StyledButton
                                color="secondary"
                                margin={10}
                                onClick={onCancel}
                            >
                                Cancel
                            </StyledButton>
                        </div>
                    </div>
                </Row>
            </form>
            <CustomModal
                show={alertModal}
                modelCloseHnd={() => {
                    setOpenAlertModal(false);
                }}
            />
        </>
    );
};

export default AddNewDocument;
