import { AgGridReact } from "ag-grid-react";
import { ColDef, ColGroupDef } from "ag-grid-community";
import { useState } from "react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";

const StateInitiatives = () => {
    const LinkRender = (item: any) => {
        return (
            <>
                {item.value.map((linkData: string) => {
                    // eslint-disable-next-line react/jsx-no-target-blank

                    return (
                        // eslint-disable-next-line react/jsx-no-target-blank
                        <a href={linkData} target="_blank" key={linkData}>
                            {linkData}
                        </a>
                    );
                })}
            </>
        );
    };
    const columnFields: (ColDef | ColGroupDef)[] = [
        {
            field: "State",
            headerName: "State",
            width: 200,
        },
        {
            field: "resource",
            headerName: "Resource URL",
            filter: false,
            width: 800,
            cellRenderer: LinkRender,
        },
    ];

    const tableRowData = [
        {
            State: "Michigan",
            resource: [
                "https://www.michigan.gov/mdhhs/0,5885,7-339-71550_2955_2985_79566---,00.html",
            ],
        },
        {
            State: "New York",
            resource: [
                "https://nyshealthfoundation.org/grantee-story/pursuing-racial-health-equity/#:~:text=For%20NYSHealth%2C%20health%20equity%20means%20that%20everyone%20has,the%20health%20needs%20of%20New%20Yorkers%20of%20color.",
            ],
        },
        {
            State: "New Jersey",
            resource: [
                "https://www.nj.gov/health/healthynj/2030/topics-objectives/cross-cutting-issues/equity/",
            ],
        },
        {
            State: "Pennsylvania",
            resource: [
                "https://www.health.pa.gov/topics/Health-Equity/Pages/Health%20Equity.aspx",
                "https://www.dhs.pa.gov/HealthInnovation/Pages/HealthEquity.aspx",
            ],
        },
    ];

    const [columnDefs, setColumnDefs] = useState(columnFields);

    const [rowData, setRowData] = useState(tableRowData);

    return (
        <>
            <AgGridReact
                pagination
                rowData={rowData}
                columnDefs={columnDefs}
                domLayout="autoHeight"
                className="ag-theme-alpine"
            />
        </>
    );
};

export default StateInitiatives;
