/* eslint-disable react/self-closing-comp */
import React, { useState, useCallback, useRef, useMemo, FC } from "react";
import { AgGridReact } from "ag-grid-react";
import { ColDef, ColGroupDef } from "ag-grid-community";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import { Checkbox } from "@doar/components";
import { Star } from "react-feather";

import {
    documentsData,
    getColumnFields,
    IDocumentSummaryTable,
} from "./summaryTableData";

const DocumentSummaryTable = () => {
    // eslint-disable-next-line prettier/prettier
    const [rowData, setRowData] =
        useState<IDocumentSummaryTable[]>(documentsData);

    const onCheckBoxClicked = (data: IDocumentSummaryTable) => {
        const rowData2 = { ...rowData };
        const rowIndex = rowData2.indexOf(data);
        // eslint-disable-next-line no-param-reassign
        data.isSelected = !data.isSelected;
        rowData2[rowIndex] = data;
        setRowData(rowData2);
    };

    const onFavClick = useCallback((data: IDocumentSummaryTable) => {
        const rowIndex = rowData.indexOf(data);
        // eslint-disable-next-line no-param-reassign
        data.isFavorite = !data.isFavorite;
        setRowData({ ...rowData, [rowIndex]: data });
    }, []);

    const CheckBoxRow: FC = (params: any) => {
        return (
            <div style={{ marginTop: 8 }}>
                <Checkbox
                    checked={params.data.isSelected}
                    onChange={() => {
                        // eslint-disable-next-line no-param-reassign
                        onCheckBoxClicked(params.data);
                    }}
                    label=""
                    id=""
                    name=""
                />
            </div>
        );
    };

    const RenderFavorite: FC = (params: any) => {
        if (params.data.isFavorite) {
            return (
                <Star
                    strokeWidth={1}
                    style={{ marginTop: 5 }}
                    color="black"
                    fill="black"
                    onClick={() => {
                        onFavClick(params.data);
                    }}
                />
            );
        }
        return (
            <Star
                strokeWidth={1}
                style={{ marginTop: 5 }}
                onClick={() => {
                    onFavClick(params.data);
                }}
            />
        );
    };
    const [columnDefs, setColumnDefs] = useState<(ColDef | ColGroupDef)[]>(
        getColumnFields(CheckBoxRow, RenderFavorite)
    );

    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    const getRowId = useCallback((params) => params.data.id, []);

    const defaultColDef: ColDef = useMemo(() => {
        return {
            filter: true,
            resizable: true,
            width: 150,
            sortable: true,
            // filterParams: {
            //     buttons: ["reset", "apply"],
            //     debounceMs: 200,
            // },
        };
    }, []);

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const gridRef: any = useRef();

    // useEffect(() => {
    //     gridRef.current.api.sizeColumnsToFit();
    // }, []);

    const onBtnExport = useCallback(() => {
        gridRef.current.api.exportDataAsCsv();
    }, []);

    return (
        <>
            <AgGridReact
                ref={gridRef}
                defaultColDef={defaultColDef}
                pagination
                rowData={rowData}
                columnDefs={columnDefs}
                getRowId={getRowId}
                domLayout="autoHeight"
                animateRows
                className="ag-theme-alpine"
            ></AgGridReact>
        </>
    );
};

export default DocumentSummaryTable;
