// A mock function to mimic making an async request for data

import {
    adminUserData,
    ILoginForm,
    IUserModel,
} from "src/models/auth/user.model";

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const authenticate = (loginFormData: ILoginForm): any => {
    return new Promise<{
        data: { status: boolean; userData: IUserModel | null };
    }>((resolve) =>
        setTimeout(() => {
            if (loginFormData.email === "admin@admin.com") {
                resolve({
                    data: { status: true, userData: adminUserData },
                });
            } else {
                resolve({
                    data: { status: false, userData: null },
                });
            }
        }, 500)
    );
};

export { authenticate };
