/* eslint-disable @typescript-eslint/restrict-plus-operands */
import { Col, FormGroup, Input, Label, Row } from "@doar/components";
import { FC, useState } from "react";
import { Dropdown, IDropdownStyles } from "@fluentui/react";
import { Upload } from "react-feather";
import { StyledButton } from "src/layouts/welcome-area/style";
import CustomModal from "./customModal";
import CustomCalender from "../../components/fluentUi-Calender/customCalender";
import InitiativePivots from "./initiativePivots";

interface IAddNewInitiative {
    onCancel: any;
}
const AddNewInitiative: FC<IAddNewInitiative> = (props: IAddNewInitiative) => {
    const { onCancel } = props;
    const [alertModal, setOpenAlertModal] = useState(false);
    const onChangeCategory = (event: any, item: any): void => {};
    const onChangeSubCategory = (event: any, item: any): void => {};
    const onAlertCallack = (event: any, item: any): void => {
        if (item.text === "Yes") {
            setOpenAlertModal(true);
        }
    };

    const formData = [
        {
            label: "Program",
            type: "text",
            id: "program",
            placeholder: "Add a program",
        },
        {
            label: "Assignee",
            type: "dropdown",
            id: "assignee",
            menu: [
                { text: "Mary Jones", key: "assignee1" },
                { text: "John Snow", key: "assignee2" },
                { text: "Beth Page", key: "assignee3" },
            ],
            onChangeCallback: onChangeSubCategory,
        },
        {
            label: "Effective Date",
            type: "date",
            id: "effectiveDate",
            placeholder: "",
        },
        {
            label: "Renewal Date",
            type: "date",
            id: "renewalDate",
            placeholder: "",
        },
        {
            label: "Status",
            type: "dropdown",
            id: "status",
            menu: [
                { text: "Active", key: "status1" },
                { text: "Out Of Date", key: "status2" },
                { text: "Archived", key: "status3" },
            ],
        },
    ];

    // const [addNewInitiativeData, setFormData] = useState(formData);

    const inputElement = (item: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={item.id}>
                    {item.label}
                </Label>
                <Input
                    type={item.type}
                    id={item.id}
                    placeholder={item.placeholder}
                    name={item.id}
                />
            </FormGroup>
        );
    };

    const dropdownStyles: Partial<IDropdownStyles> = {
        dropdown: { width: "100%" },
        root: { height: "100%" },
    };

    const dropdownElement = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <Dropdown
                    // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                    placeholder={`Select an ${data.label}`}
                    options={data.menu}
                    styles={dropdownStyles}
                    onChange={data.onChangeCallback}
                />
            </FormGroup>
        );
    };

    const renderCalender = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>
                <CustomCalender />
            </FormGroup>
        );
    };

    const renderButton = (data: any) => {
        return (
            <FormGroup mb="20px">
                <Label display="block" mb="5px" htmlFor={data.id}>
                    {data.label}
                </Label>

                <StyledButton fullwidth hasIcon>
                    <Upload />
                    {data.buttonText}
                </StyledButton>
            </FormGroup>
        );
    };
    const oneRowItem = 5;

    const renderElement = (item: any) => {
        if (item.type === "text") {
            return inputElement(item);
        }
        if (item.type === "dropdown") {
            return dropdownElement(item);
        }
        if (item.type === "date") {
            return renderCalender(item);
        }
        if (item.type === "button") {
            return renderButton(item);
        }
        return <></>;
    };

    const chunk: any = (array: any, chunksize: number) => {
        const newArray = [];
        while (array.length) {
            const chunks = array.splice(0, chunksize);
            newArray.push(chunks);
        }
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return newArray;
    };

    const splitedAaddNewInitiativeData = chunk(formData, oneRowItem);
    return (
        <>
            <form action="#" noValidate>
                {splitedAaddNewInitiativeData.map(
                    (oneRowData: [], index: number) => {
                        return (
                            // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
                            <Row key={`abc${0 + index}`}>
                                {oneRowData.map((eleData) => {
                                    return (
                                        <Col
                                            col={6}
                                            sm={4}
                                            md={3}
                                            lg
                                            mt="10px"
                                            key={`col${0 + index}`}
                                        >
                                            {renderElement(eleData)}
                                        </Col>
                                    );
                                })}
                            </Row>
                        );
                    }
                )}
                <Row>
                    <Col lg={12} xl={12} mt="10px">
                        <InitiativePivots />
                    </Col>
                </Row>
                <Row>
                    <Col lg={12} xl={12} mt="10px">
                        <div style={{ width: "100%" }}>
                            <div
                                style={{
                                    float: "right",
                                    marginRight: 50,
                                }}
                            >
                                <StyledButton
                                    type="submit"
                                    margin={10}
                                    disabled
                                >
                                    Add Entry
                                </StyledButton>
                                <StyledButton
                                    margin={10}
                                    onClick={onCancel}
                                    color="secondary"
                                >
                                    Cancel
                                </StyledButton>
                            </div>
                        </div>
                    </Col>
                </Row>
            </form>
            <CustomModal
                show={alertModal}
                modelCloseHnd={() => {
                    setOpenAlertModal(false);
                }}
            />
        </>
    );
};

export default AddNewInitiative;
