import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { ILoginForm, IUserModel } from "src/models/auth/user.model";
import { authenticate } from "src/services/auth/api";

export interface AuthState {
    title: string;
    status: "idle" | "loading" | "failed";
    loggedInUserData: IUserModel | null;
}

const initialState: AuthState = {
    title: "Home page",
    status: "idle",
    loggedInUserData: null,
};

export const authenticateUserAsync = createAsyncThunk(
    "counter/fetchCount",
    async (loginFormData: ILoginForm) => {
        const response = await authenticate(loginFormData);
        // The value we return becomes the `fulfilled` action payload
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return response.data;
    }
);

const dashboardSlice = createSlice({
    name: "auth",
    initialState,
    reducers: {
        changeTitle: (state) => {
            return { ...state, title: "New Updated Dashboard Title" };
        },
    },
    // The `extraReducers` field lets the slice handle actions defined elsewhere,
    // including actions generated by createAsyncThunk or in other slices.
    extraReducers: (builder) => {
        builder
            .addCase(authenticateUserAsync.pending, (state) => {
                return { ...state, status: "loading" };
            })
            .addCase(authenticateUserAsync.fulfilled, (state, action) => {
                return {
                    ...state,
                    status: "idle",
                    loggedInUserData: action.payload.userData,
                };
            });
    },
});

// export const dashboardStateSelector = (state: RootState) => state.dashboard;
export const { changeTitle } = dashboardSlice.actions;
export default dashboardSlice.reducer;
