import React, { Suspense, lazy, FC } from "react";
import {
    Navigate,
    BrowserRouter as Router,
    Routes,
    Route,
} from "react-router-dom";
import SEO from "src/components-doar/seo";
import Preloader from "./components-doar/preloader";
import DashboardMain from "./modules/dashboard/dashboard.main";
import Layout from "./layouts/vertical";
import Content from "./layouts/vertical/content";
import ContentHeader from "./layouts/vertical/content-header";
import ContentBody from "./layouts/vertical/content-body";
import { AuthState } from "./redux/slices/auth";
import { RootState } from "./redux/store";
import { useAppSelector } from "./redux/hooks";
import MetricsMain from "./modules/metrics/metrics.main";
import AssessmentMain from "./modules/assessment/assessment.main";
import InitiativesMain from "./modules/initiatives/Initiatives.main";
import TrainingMain from "./modules/training/training.main";
import DocumentsMain from "./modules/documents/documents.main";
import AccreditationMain from "./modules/accreditation/accreditation.main";
import ResourcesMain from "./modules/resources/resources.main";

const SignIn = lazy(() => import("./modules/auth/signin"));
const SignUp = lazy(() => import("./pages/classic-plus/signup"));
const ForgotPassword = lazy(
    () => import("./pages/classic-plus/forgot-password")
);

interface IPrivateRoute {
    component: any;
}

const PrivateRoute = ({ component: Component, ...rest }: IPrivateRoute) => {
    const authState: AuthState = useAppSelector(
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        (state: RootState) => state.auth
    );

    const isAuthenticated = (): boolean => {
        return true || !!authState.loggedInUserData;
    };

    return isAuthenticated() ? (
        <Layout>
            <SEO />
            <Content>
                <ContentHeader />
                <ContentBody container={false}>
                    <Component {...rest} />
                </ContentBody>
            </Content>
        </Layout>
    ) : (
        <Navigate to="/signIn" />
    );
};

const App: FC = () => {
    return (
        <>
            <Router>
                <Suspense fallback={<Preloader />}>
                    <Routes>
                        <Route path="/signIn" element={<SignIn />} />
                        <Route path="/signUp" element={<SignUp />} />
                        <Route
                            path="/forgot-password"
                            element={<ForgotPassword />}
                        />

                        <Route
                            path="/"
                            element={<PrivateRoute component={DashboardMain} />}
                        />

                        <Route
                            path="/metrics"
                            element={<PrivateRoute component={MetricsMain} />}
                        />

                        <Route
                            path="/assessment"
                            element={
                                <PrivateRoute component={AssessmentMain} />
                            }
                        />

                        <Route
                            path="/initiatives"
                            element={
                                <PrivateRoute component={InitiativesMain} />
                            }
                        />

                        <Route
                            path="/training"
                            element={<PrivateRoute component={TrainingMain} />}
                        />

                        <Route
                            path="/documents"
                            element={<PrivateRoute component={DocumentsMain} />}
                        />

                        <Route
                            path="/accreditation"
                            element={
                                <PrivateRoute component={AccreditationMain} />
                            }
                        />

                        <Route
                            path="/resources"
                            element={<PrivateRoute component={ResourcesMain} />}
                        />
                    </Routes>
                </Suspense>
            </Router>
        </>
    );
};

export default App;
