/* eslint-disable react/self-closing-comp */
import React, { useState, useCallback, useRef, useMemo, FC } from "react";
import { AgGridReact } from "ag-grid-react";
import { ColDef, ColGroupDef } from "ag-grid-community";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import { Checkbox } from "@doar/components";
import { Star } from "react-feather";

import {
    documentsData,
    getColumnFields,
    IInitiativeSummaryTable,
} from "./summaryTableData";

const InitiativeSummaryTable = () => {
    // eslint-disable-next-line prettier/prettier
    const [rowData, setRowData] =
        useState<IInitiativeSummaryTable[]>(documentsData);

    const [columnDefs, setColumnDefs] = useState<(ColDef | ColGroupDef)[]>(
        getColumnFields()
    );

    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    const getRowId = useCallback((params) => params.data.id, []);

    const defaultColDef: ColDef = useMemo(() => {
        return {
            filter: true,
            resizable: true,
            width: 150,
            sortable: true,
            // filterParams: {
            //     buttons: ["reset", "apply"],
            //     debounceMs: 200,
            // },
        };
    }, []);

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const gridRef: any = useRef();

    // useEffect(() => {
    //     gridRef.current.api.sizeColumnsToFit();
    // }, []);

    const onBtnExport = useCallback(() => {
        gridRef.current.api.exportDataAsCsv();
    }, []);

    return (
        <>
            <AgGridReact
                ref={gridRef}
                defaultColDef={defaultColDef}
                pagination
                rowData={rowData}
                columnDefs={columnDefs}
                getRowId={getRowId}
                domLayout="autoHeight"
                animateRows
                className="ag-theme-alpine"
            ></AgGridReact>
        </>
    );
};

export default InitiativeSummaryTable;
