import { FC, useState } from "react";
import { Col, Row } from "@doar/components";
import WelcomeArea from "src/layouts/welcome-area";
import InitiativeWelcomeArea from "./initiativeWelcomeArea";
import AddNewInitiative from "./addNewInitiative";
import InitiativeSummaryTable from "./summaryTable";

const InitiativesMain: FC = () => {
    const [openAddNewInitiative, setAddNewInitiative] = useState(false);
    const onCSVExport = () => {};
    return (
        <>
            <WelcomeArea
                welcomeText="Welcome To Initiative"
                title="Initiative"
                prevTitle="HEQ 360"
            >
                <InitiativeWelcomeArea
                    onExportHnd={onCSVExport}
                    disableAddNewBtn={openAddNewInitiative}
                    addNewDocumentHnd={() => {
                        setAddNewInitiative(true);
                    }}
                />
            </WelcomeArea>
            <Row gutters={10}>
                <Col lg={12} xl={12} mt="10px">
                    {openAddNewInitiative ? (
                        <AddNewInitiative
                            onCancel={() => {
                                setAddNewInitiative(false);
                            }}
                        />
                    ) : (
                        <InitiativeSummaryTable />
                    )}
                </Col>
            </Row>
        </>
    );
};

export default InitiativesMain;
