import { FC, useState } from "react";
import WelcomeArea from "src/layouts/welcome-area";

import { Col, Row } from "@doar/components";
import DocumentsWelcomeArea from "./welcomeArea";
import AddNewDocument from "./addNewDocument";
import DocumentSummaryTable from "./summaryTable";

const DocumentsMain: FC = () => {
    const [openAddNewDocument, setAddNewDocument] = useState(false);
    const onCSVExport = () => {};
    return (
        <>
            <WelcomeArea
                welcomeText="Welcome To Documents"
                title="Documents"
                prevTitle="HEQ 360"
            >
                <DocumentsWelcomeArea
                    onExportHnd={onCSVExport}
                    disableAddNewBtn={openAddNewDocument}
                    addNewDocumentHnd={() => {
                        setAddNewDocument(true);
                    }}
                />
            </WelcomeArea>
            <Row gutters={10}>
                <Col lg={12} xl={12} mt="10px">
                    {openAddNewDocument ? (
                        <AddNewDocument
                            onCancel={() => {
                                setAddNewDocument(false);
                            }}
                        />
                    ) : (
                        <DocumentSummaryTable />
                    )}
                </Col>
            </Row>
        </>
    );
};

export default DocumentsMain;
